import React from 'react';
import { Panel, PanelHeader, PanelHeaderBack, 
FixedLayout, Tabs, TabsItem, Div } from '@vkontakte/vkui';
import Icon16Fire from '@vkontakte/icons/dist/16/fire';

import styles from "../styles/custom-styles.sass";
import ListUsers from './ListUsers';

const Top = ({go, id, tabGeneral, tabFriends, tabGroups, 
     activeTab, userPlace, rating, dataUsers, goHome}) => {

        const renderUsers = dataUsers.map((item, index) => {
            return (
                <ListUsers
                    key={item.id}
                    id={index + 1}
                    idVk={item.id}
                    photoURL={item.photoURL}
                    firstName={item.firstName}
                    lastName={item.lastName}
                    rating={item.rating}
                    amount={item.durkaCoins}
                />
            );
        });



    return (
        <Panel id={id}>
            <PanelHeader left={<PanelHeaderBack onClick={goHome} data-to="home" />}>
                Топ
            </PanelHeader>

            <FixedLayout vertical="top" filled={true}>
                <Tabs >
                    <TabsItem
                        onClick={tabGeneral}
                        selected={activeTab === 'general'}
                    >
                        Общий
                    </TabsItem>
                    <TabsItem
                        onClick={tabFriends}
                        selected={activeTab === 'friends'}
                    >
                        Друзья
                    </TabsItem>
                    <TabsItem
                        onClick={tabGroups}
                        selected={activeTab === 'groups'}
                    >
                        Сообщества
                    </TabsItem>
                </Tabs>
            </FixedLayout>




            {activeTab === 'general'
                ?
                <>
                    <Div className="headerTop">
                        Ваше место: {userPlace}
                        <>
                            &#12288;{`Ваш Рейтинг:`}
                            <Icon16Fire className="SUKA" fill="red" />
                            {rating}
                        </>
                    </Div>

                    <Div>
                        {renderUsers}
                    </Div>
                </>
                :
                ''
            }


            {activeTab === 'friends'
                ?
                <>
                    <Div className="headerTop">
                        Ваше место: {userPlace}
                        <>
                            &#12288;{`Ваш Рейтинг:`}
                            <Icon16Fire className="SUKA" fill="red" />

                            {rating}
                        </>
                    </Div>

                    <Div className="stub">
                        Раздел в разработке...
                    </Div>
                </>
                :
                ''
            }


            {activeTab === 'groups'
                ?
                <>
                    <Div className="headerTop">
                        Раздел в разработке...
                    </Div>
                </>
                :
                ''
            }

        </Panel>
    );
};

export default Top;
